<template>
  <div class="home">
    <!-- <van-grid
      clickable
      :column-num="4"
    >
      <van-grid-item
        icon="home-o"
        text="跳转"
        @click="toBillOfFare"
      />
    </van-grid> -->
    <van-form @submit="onSubmit">
      <van-cell-group inset>
        <van-field
          name="stepper"
          label="素菜食材数量"
        >
          <template #input>
            <van-stepper
              v-model="vegetableNum"
              min="1"
              max="5"
            />
          </template>
        </van-field>
        <van-field
          name="stepper"
          label="荤菜食材数量"
        >
          <template #input>
            <van-stepper
              v-model="meatNum"
              min="1"
              max="5"
            />
          </template>
        </van-field>
      </van-cell-group>
      <div style="margin: 16px;">
        <van-button
          round
          block
          type="primary"
          native-type="submit"
        >
          生成
        </van-button>
      </div>
    </van-form>
    <div class="mt20">
      <div>素菜：
        <span
          class="mr5"
          v-for="item in choosedVegetable"
          :key="item"
        >{{item}}</span>
      </div>
      <div>荤菜：
        <span
          class="mr5"
          v-for="item in choosedMeat"
          :key="item"
        >{{item}}</span>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src

export default {
  name: "Home",
  components: {
  },
  data () {
    return {
      vegetableData: ["土豆", "番茄", "茭白", "青豆", "豆芽", "大白菜", "白萝卜", "胡萝卜",],
      meatData: ["猪肉", "牛肉", "羊肉", "鱼肉", "虾仁",],
      vegetableNum: 1,
      meatNum: 1,
      choosedVegetable: [],
      choosedMeat: []
    }
  },
  methods: {
    toBillOfFare () {
      console.log('123123')
      console.log(this.$router)
      this.$router.push({ name: 'BillOfFare' })
    },
    onSubmit () {
      console.log('123123')
      console.log(this.vegetableNum)
      console.log(this.meatNum)
      this.choosedVegetable = this.getRandomArrayElements(this.vegetableData, this.vegetableNum)
      this.choosedMeat = this.getRandomArrayElements(this.meatData, this.meatNum)
    },
    getRandomArrayElements (arr, count) {
      var shuffled = arr.slice(0),
        i = arr.length,
        min = i - count,
        temp, index;
      while (i-- > min) {
        index = Math.floor((i + 1) * Math.random());
        temp = shuffled[index];
        shuffled[index] = shuffled[i];
        shuffled[i] = temp;
      }
      return shuffled.slice(min);
    },
  }
};
</script>
<style scoped>
.mt20 {
  margin-top: 20px;
}
.mr5 {
  margin-right: 5px;
}
</style>