import {
    createApp
} from 'vue'
import App from './App.vue'
import router from './router'
import 'vant/lib/index.css'
import {
    Button,
    Grid,
    GridItem,
    Form, Field, CellGroup, Stepper
} from 'vant'

const app = createApp(App)

app.use(router)
    .use(Button)
    .use(Grid)
    .use(GridItem)
    .use(Form)
    .use(Field)
    .use(CellGroup)
    .use(Stepper)
    .mount('#app')
